.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

p,h1,h2,h3,h4{
  font-family: Helvetica, Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

h1{
  font-size: calc(10px + 6vmin);
  font-weight: 700;
}

h2{
  font-size: calc(10px + 4vmin);
  font-weight: 400;
}

.NavbarTop{
  margin-bottom: 55px;
}

.body{
  text-align: center;
  font-size: calc(10px + 2vmin);
  min-height: 50vh;
  /*background-color: #282c34;*/
  color: black;
  
}


.presentation_container {
  display: flex;
  
  flex-flow: row wrap;
  justify-content: space-around;
  
  padding: 0;
  margin: 0;
  list-style: none;
  align-items: baseline;
  max-width: 100%;
}

.presentation-item {
  object-fit: contain;
  overflow: hidden; /*Changer pour permettre */
  padding: 0px;
  max-width: 100%;
  max-height: auto;
  margin-top: 10px;
  line-height: 150px;
  font-weight: bold;
  font-size: 3em;
  text-align: center;
  font-weight: 800;
  
}